import React, { Component } from 'react';
import { translate } from 'react-i18next';
import Button from '@material-ui/core/Button';
import facebook from '../img/facebook.svg';
import instagram from '../img/instagram.svg';
import skype from '../img/skype.svg';
import github from '../img/github.svg';

export class Contacts extends Component {
  render() {
    const { t, data } = this.props;

    return (
      <div className="contacts">
        <p>
          <b>{t('UI:status', 'Status')}:</b> {t('current_status', 'Employed')}
        </p>
        <b>{t('UI:contacts', 'Contacts')}:</b>{' '}
        {data && (
          <React.Fragment>
            {
              <Button size="small" href={`mailto:${data.email}`}>
                {data.email}
              </Button>
            }
            ,{' '}
            {
              <Button size="small" href={`skype:${data.skype}?chat`}>
                <img src={skype} alt="skype" />
                {data.skype}
              </Button>
            }
            ,{' '}
            {
              <Button size="small" target="_blank" href={data.facebook}>
                <img src={facebook} alt="facebook" />
                {t('user_facebook', 'wnetking')}
              </Button>
            }
            ,{' '}
            {
              <Button size="small" target="_blank" href={data.instagram}>
                <img src={instagram} alt="instagram" />
                {t('user_instagram', 'wnetking')}
              </Button>
            }
            ,{' '}
            {
              <Button size="small" target="_blank" href={data.github}>
                <img src={github} alt="github" />
                {t('user_github', 'wnetking')}
              </Button>
            }
          </React.Fragment>
        )}
      </div>
    );
  }
}

export default translate(['User', 'UI'])(Contacts);



