import React from 'react';
import { translate } from 'react-i18next';
import Tooltip from '@material-ui/core/Tooltip';

const Skills = ({ t, data }) => {
  return (
    <p>
      <b>{t('i_have_skills', 'I have skills in')}:</b>{' '}
      {data &&
        data.skills.map(
          (item, index) =>
            item && (
              <React.Fragment key={index}>
                <Tooltip title={`${item.count}/100`} placement="top-start">
                  <span className="skill">{item.name}</span>
                </Tooltip>
              </React.Fragment>
            )
        )}
    </p>
  );
};

export default translate('UI')(Skills);



