import React, { Component } from 'react';
import { translate, Trans } from 'react-i18next';
import { Link } from 'react-router-dom';
import Animated from './Animated';
import Skills from './Skills';
import Contacts from './Contacts';
import * as routes from '../constants/routes';

class Home extends Component {
  render() {
    const { t, data } = this.props;

    return (
      <Animated>
        <div>
          <p className="description">
            <Trans i18nKey="user_about">
              Hi, there! I'm Frontend developer with 3+ years of experience in
              JS. I've got a profound knowledge of JS (ECMAScript 6) and JS
              frameworks, such as jQuery, React.js. I have expert-level skills
              in HTML coding using HTML5 and CSS3. Look at my
              <Link to={routes.EXPERIENCE}> expirience</Link> and
              <Link to={routes.EDUCATION}> education</Link>. I like to visit
              different frontend
              <Link to={routes.COURSES}> events</Link>.
            </Trans>
          </p>
          <Skills data={data} />
          <Contacts data={data} />
          <p />
          <p>{t('thank_you', 'Thank you that visited this page!')}</p>
          <p>{t('best_regard', 'Best regard!')}</p>
        </div>
      </Animated>
    );
  }
}

export default translate('User')(Home);



